import { useState , useEffect,useCallback } from "react";
import BasicExample from "../util/Table"
import styles from "../styles/SearchBar.module.css";
import axios from "axios";
import Table from 'react-bootstrap/Table';
import { useDebounce } from 'react-use';

function SearchBar(props){
    
    
    const [text, settext] = useState("");
    const [isLoading, setisLoading] = useState(false);
    const [isFetching, setisFetching] = useState(false);
    const [timer, setTimer] = useState(0); // 디바운싱 타이머
    const [searchHashTags, setsearchHashTags] = useState([]);
    
    
    
    const pressEnter = (e) =>{
        if(e.key ==='Enter'){
            window.location.href=`/search/${encodeURIComponent(text)}`
        }
    }

    const onChange = (e) =>{      
        settext(e.target.value)    
    }
    const moveToSearchHome = (e)=>{
       console.log(text);
       window.location.href=`/search/${encodeURIComponent(text)}`
    }
 
      const fetchHashTags = useCallback(async (searchValue) => {
        axios.get(`/hashTag/list/${searchValue}`,{
            params: { page:0, size: 3 },
        }).then(({data})=>{
            setsearchHashTags(data.content)
            setisFetching(false)
            
            })   
      },[text])
     
    
      
    const onKeyUp = (e)=>{
        setisFetching(true)
         
        let text = e.target.value
        
        if(text.startsWith('#')){
            if(text.length>1){
                let searchValue = text.substring(1)
                if (timer) {
                    clearTimeout(timer);
                  }
                  const newTimer = setTimeout(async () => {
                    try {
                      await fetchHashTags(searchValue);
                    } catch (e) {
                      console.error('error', e);
                    }
                  }, 300);
                  setTimer(newTimer);
                
            }
          
        }else{
            return;
        }                
         
    }

    const chooseHashTag = (e) =>{
        if(e.target.className !=='tagtd') return;
        let tagContent = e.target.firstChild.innerText;
        console.log(tagContent);
        
        settext(tagContent) 
        setisFetching(true)
      }

     
    return (
        <div onKeyDown={pressEnter}>
          
        <input type="text" value={text} onChange={onChange} onKeyUp={onKeyUp} ></input>
        <button onClick={moveToSearchHome}>검색</button>
        <div>
           {isFetching ? null : 
           <Table style={{ width: "20%", margin: "0 auto"}} striped bordered hover >
           <tbody>
             {searchHashTags.map(hashtag=>(
               <tr  onClick={chooseHashTag}>
                 <td className="tagtd">
                   <span className="tagspan">{hashtag.content}</span><br></br>
                   <span>{hashtag.feedCount}</span>
                 </td>
               </tr>
             )
             )}
           </tbody>
         </Table>
           }
           
        </div>

    </div>
    )
}
export default SearchBar;