
import { useState , useEffect} from "react";
import styles from "../styles/FeedComment.module.css";

function FeedComment(props){
   const [replyShow, setReplyShow] = useState(false);
   
    return (
        <div className={styles.commentBox}>
            
            <span>작성자 : {props.feedComment.feedUserId}</span>
            <p>{props.feedComment.comment}</p>
            <div>
                {/* <button>좋아요</button> */}
                <button onClick={()=>props.setstate({
                     ...props.state,
                    replyTofeedUserId : props.feedComment.feedUserId,
                    parentId:props.feedComment.feedCommentId
                })} >대댓글 달기</button>
            </div>
            {props.feedComment.children.length>0 
            ? <button className={styles.button2} onClick={()=>setReplyShow(true)}>답글보기({props.feedComment.children.length})</button>
            :  null  }
            
            {replyShow
            ? 
            <div>
            {props.feedComment.children.map(childReply =>
               <FeedComment 
               feedComment={childReply}
               state={props.state}
               setstate={props.setstate}
               ></FeedComment>
                
            )}
            </div>
            : ""}
        </div>
    )
}

export default FeedComment;